"use client"

import type React from "react"
import { useState } from "react"
import { Mail, User, CreditCard, CheckCircle2, Phone } from "lucide-react"

export default function SubscriptionForm() {
  const [formData, setFormData] = useState({
    nombre: "",
    email: "",
    phone: "",
    plan: "clasica",
  })

  const [submitted, setSubmitted] = useState(false)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState("")

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError("")

    try {
      const response = await fetch(
        process.env.NEXT_PUBLIC_BACKEND_URL + "/subscriptions", // üëà Ruta real del backend
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(formData),
        }
      )

      if (!response.ok) throw new Error("Error al procesar la suscripci√≥n")

      setSubmitted(true)
      setTimeout(() => setSubmitted(false), 3000)

      // Limpiar fields
      setFormData({ nombre: "", email: "", phone: "", plan: "clasica" })
    } catch (err) {
      setError("Hubo un error. Intenta nuevamente.")
      console.error(err)
    } finally {
      setLoading(false)
    }
  }

  return (
    <section id="contacto" className="px-6 py-24 max-w-3xl mx-auto">

      <form onSubmit={handleSubmit} className="space-y-6">

        {/* Nombre */}
        <div>
          <label htmlFor="nombre">Nombre completo</label>
          <input
            id="nombre"
            value={formData.nombre}
            onChange={(e) => setFormData({ ...formData, nombre: e.target.value })}
            required
          />
        </div>

        {/* Email */}
        <div>
          <label htmlFor="email">Correo electr√≥nico</label>
          <input
            id="email"
            type="email"
            value={formData.email}
            onChange={(e) => setFormData({ ...formData, email: e.target.value })}
            required
          />
        </div>

        {/* Tel√©fono */}
        <div>
          <label htmlFor="phone">Tel√©fono</label>
          <input
            id="phone"
            value={formData.phone}
            onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
            required
          />
        </div>

        {/* Plan */}
        <div>
          <label htmlFor="plan">Selecciona tu membres√≠a</label>
          <select
            id="plan"
            value={formData.plan}
            onChange={(e) => setFormData({ ...formData, plan: e.target.value })}
          >
            <option value="clasica">Cl√°sica - $8.500/mes</option>
            <option value="premium">Premium - $12.500/mes</option>
            <option value="elite">Elite - $22.500/mes</option>
          </select>
        </div>

        <button type="submit" disabled={loading || submitted}>
          {loading
            ? "Enviando..."
            : submitted
            ? "¬°Enviado correctamente!"
            : "Suscribirme ahora"}
        </button>

      </form>

      {error && <p className="text-red-500 mt-4">{error}</p>}
    </section>
  )
}
